import {useEffect} from 'react';
import AOS from 'aos';
import 'aos/dist/aos.css';
import {
    Typography,
    Grid,
  } from "@mui/material";
import Proyects from "./Proyects";
function GridProyects() {
  useEffect(() => {
    AOS.init({duration:2000})
  }, [])
  return (
    <Grid
        container
        direction="column"
        justifyContent="space-around"
        alignItems="center"
        mb={5}
        >
                <Grid item xs={4}>
                  <div data-aos = "fade-up">
                    <Typography variant='h4' mt={15} mb={2} pr={5} pl={5}>
                        Mis proyectos de inicio
                    </Typography>
                  </div>   
                </Grid>
                <Grid item xs={4}>
                  <div data-aos = "fade-up">
                    <Typography variant='body1' mt={2} mb={10} pr={8} pl={8}>
                        Soy adicto a los productos digitales. A lo largo de mi carrera, he desarrollado cientos de aplicaciones web, de escritorio, mviles y video juegos en diferentes tecnologas y frameworks. Eventualmente, decid que sera un desafo divertido intentar disear y construir mi portafolio.
                    </Typography>
                  </div> 
                </Grid>
                <Proyects />
    </Grid>
    

  )
}

export default GridProyects